// Media Query Manager

@mixin respond($breakpoint) {
    @if $breakpoint == phone {
        @media (max-width: 600px) {
            @content; } }
    @if $breakpoint == tab-port {
        @media (max-width: 768px) {
            @content; } }
    @if $breakpoint == tab-land {
        @media (max-width: 1120px) {
            @content; } } }


