.row {
  display: flex;
  flex-wrap: wrap; }

$columns: 12;

@for $i from 1 through $columns {
  .col-#{$i} {
    flex: 0 0 100% / $columns * $i; } }


// Responsive Grid
@include respond(tab-port) {
  div[class^="col-"] {
    flex: 0 0 100%; } }
